<template>
    <div class="md-example-child md-example-child-swiper md-example-child-swiper-0">
    <md-swiper
      @before-change="beforeChange"
      @after-change="afterChange"
      :autoplay="3000"
      ref="swiper">
      <md-swiper-item :key="$index" v-for="(item, $index) in simple">
        <a href="javascript:void(0)"
          class="banner-item"
          :style="{'background': `${item.color}`}">
            <img class="banner-img" :src="item.url">
          </a>
      </md-swiper-item>
    </md-swiper>
  </div>
</template>

<script>
import { Swiper, SwiperItem } from 'mand-mobile'
import simple from 'mand-mobile/components/swiper/demo/data/simple'
export default {
    name:'home-banner',
     components: {
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
  },
  data() {
    return {
       simple:[
       {
          id:1,
          color:'red',
          url:'https://cimgs1.fenqile.com/ibanner2/M00/32/77/jqgHAFwArNWAW-LZAAEKzEBCIrQ810_750x320.jpg'
        },
        {
            id:2,
            color:'red',
            url:'http://m.cignacmb.com/d/file/20180907/f9c4c7abe082f1966f991693a3ff70ab.jpg'
        },
        {
            id:2,
            color:'red',
            url:'https://cimgs1.fenqile.com/ibanner2/M00/33/55/jagHAFwE6SqAM5iiAAFvcyjOYNU721_750x320.jpg'
        },
      ]
    }
  },
  mounted() {
   
  },
  methods: {
    setValue(id, value) {
      document.querySelector(id) && (document.querySelector(id).innerHTML = value)
    },
    beforeChange(from, to) {
      this.setValue('#valueSwiper0', from)
      this.setValue('#valueSwiper1', to)
    },
    afterChange(from, to) {
      this.setValue('#valueSwiper2', from)
      this.setValue('#valueSwiper3', to)
    },
    fn(index) {
      this.setValue('#valueSwiper4', index)
    },
    goto() {
      this.$refs.swiper.goto()
    },
    play() {
      this.$refs.swiper.play()
    },
    stop() {
      this.$refs.swiper.stop()
    },
  }
}
</script>

<style lang="stylus" scoped>
.md-example-child >>> .md-swiper-indicator {
  width: 10px;
  height: 10px;
}
.md-example-child
  height 300px
  .banner-item
    float left
    width 100%
    height 100%
    line-height 300px
    text-align center
    font-size 28px
    color #FFF
    box-align center
    align-items center
    box-pack center
    justify-content center
    text-decoration-line none
    .banner-img
        width 100%
</style>



