<template>
    <div class="header">
       <router-link tag="div" to="/" class="input">
        <span class="iconfont icon-bg">&#xe6bc;</span>
        <span class="input-title">121活动套餐大优惠</span>
       </router-link>
        <span class="iconfont icon-right">&#xe627;</span>
    </div>
</template>

<script>
export default {
    name:'homeHader'
} 
</script> 

<style lang="stylus" scoped>
@import '~styles/base.styl';
 .header
    display:flex
    line-height:$headerHeight
    height: $headerHeight
    background:$bgColor
    color:#fff
    padding: 0 .2rem 
    align-items: center
    justify-content: space-between
    .icon-right
        padding-left .2rem
    .input
        width: 90%
        height:.60rem
        line-height:.60rem
        flex :1
        overflow: hidden
        background-color: #fff
        border-radius: 4px
        font-size: .25rem
        .input-title
            color: #808080
        .icon-bg
            padding-left .2rem
            font-size: .32rem
            color: #808080
</style>



